.wrapper {
    margin-top: 14px;
    text-align: center;
    .capture {
        width: 200px;
        height: 200px;
    }
    .title {
        margin-top: 6px;
        font-size: 24px;
        color: #000000;
        letter-spacing: -0.11px;
    }
    .content {
        margin-top: 10px;
    }
    .button {
        margin-top: 20px;
    }
}

@media screen and (min-width: 768px) {
    .wrapper {
        margin-top: 60px;
        .capture {
            width: 300px;
            height: 300px;
        }
        .title {
            margin-top: 60px;
            font-size: 28px;
            letter-spacing: -0.13px;
        }
        .content {
            margin-top: 20px;
        }
        .button {
            margin-top: 60px;
        }
    } 
}