@import '../../globalVariable';

$buttonHeight: 40px;
$iconSize: 20px;
$loadingSize: 20px;
$fontSize: 16px;

.buttonWrapper {
  position: relative;
  display: flex;
  justify-content: center;
  height: $buttonHeight;
  line-height: $buttonHeight;
  border-radius: 2px;
  cursor: pointer;
  border: 1px solid #2DBE60;
  background: $yxLightGreen;
  color: #fff;
  font-size:$middleFontSize;
  .buttonMain {
    letter-spacing: -0.28px;
    position: relative;
    font-size: $fontSize;
    .buttonIcon {
      position: absolute;
      left: 0;
      top: 50%;
      width: $iconSize;
      height: $iconSize;
      transform: translate(-($iconSize+10), -50%);
      @include hide;
    }
  }
  .loadingIcon {
    position: absolute;
    right: 10%;
    top: 50%;
    width: $loadingSize;
    height: $loadingSize;
    font-size: 0;
    transform: translate(0, -50%);
    @include hide;
  }
  &.disabled {
    background-color: #dddddd;
    border: 1px solid #dddddd;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  }
}

@media screen and (min-width: 768px) {
  .buttonWrapper {
    height: 50px;
    line-height: 50px;
    .buttonMain {
      font-size: 18px;
    }
  }
}

